.buttonsBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 458px;
  justify-content: space-between;
  background: transparent;
  @media screen and(max-width: 458px) {
    width: 280px;
  }

  .buttonLine {
    display: flex;
    justify-content: space-between;
    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      border: none;
      width: 100px;
      height: 100px;
      @media screen and(max-width: 458px) {
        width: 70px;
        height: 70px;
      }

      background: transparent;
      color: white;
      padding: 0;
      position: relative;

      cursor: pointer;

      font-family: "Geometria";
      font-style: normal;
      font-weight: 500;
      font-size: 36px;

      text-align: center;

      &:active {
        outline: none;
      }
      &:focus {
        outline: none;
      }
    }
    .buttonChild {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;

      &:hover {
        border-radius: 50px;
        background: rgba(77, 143, 197, 0.815);
      }

      &:active {
        background-color: white;
        color: rgb(48, 48, 145);
      }
    }
  }
}
